/* Sobrescribir estilos conflictivos de react-pdf */

/* Estilos para los controles de paginación de react-pdf */
.react-pdf__Page__canvas {
  display: block !important;
  margin: 0 auto !important;
  max-width: 100% !important;
  height: auto !important;
}

/* Estilos para evitar conflictos con los botones */
.pdf-controls .btn--outline,
.project-pdf-controls .btn--outline {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  text-transform: none;
}

/* Aislamiento de estilos para evitar que los estilos globales afecten al PDF */
.pdf-document-wrapper,
.project-pdf-document-wrapper {
  isolation: isolate;
}

/* Asegurar que los contenedores respeten el tema oscuro/claro */
.dark .pdf-document,
.dark .project-pdf-document {
  background-color: #2a2f4c !important;
}

.light .pdf-document,
.light .project-pdf-document {
  background-color: #fff !important;
}

/* Asegurar que las páginas del PDF se rendericen correctamente */
.react-pdf__Document {
  width: fit-content !important;
  max-width: 100% !important;
  margin: 0 auto !important;
}

/* Evitar que el PDF se corte en pantallas pequeñas */
@media (max-width: 768px) {
  .react-pdf__Page__canvas {
    width: 100% !important;
    height: auto !important;
  }
}