.app {
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  color: var(--clr-fg);
  background-color: var(--clr-bg);
}

.light {
  --clr-bg: #fcfcfc;
  --clr-bg-alt: #fff;
  --clr-fg: #555;
  --clr-fg-alt: #444;
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.dark {
  --clr-bg: #23283e;
  --clr-bg-alt: #2a2f4c;
  --clr-fg: #bdbddd;
  --clr-fg-alt: #cdcdff;
  --clr-primary: #90a0d9;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

main {
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
}

.section {
  margin-top: 5em;
}

.section__title {
  text-align: center;
  margin-bottom: 1em;
  text-transform: uppercase;
}

.center {
  display: flex;
  align-items: center;
}

.link {
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
}

.link:hover {
  color: var(--clr-primary);
}

.link::before {
  content: '';
  display: inline;
  width: 0%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
  width: 100%;

}

.link--nav {
  color: var(--clr-fg);
  text-transform: lowercase;
  padding-top: 1rem;
  font-size: 1rem;
  font-weight: 500;
}

.link--icon {
  color: var(--clr-fg);
}
.btn.btn,
button.btn.btn,
a.btn.btn {
  display: block !important;
  cursor: pointer !important;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  text-transform: lowercase;
  transition: transform 0.2s ease-in-out;
}

.btn.btn--outline,
button.btn.btn--outline,
a.btn.btn--outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;  /* XR: Originalmente relative, cómo cambiar al centro?  */
  overflow: hidden;
  z-index: 1;
}

.btn.btn--outline:hover,
.btn.btn--outline:focus {
  color: var(--clr-bg) !important;
  border-color: var(--clr-primary) !important;
}

.btn.btn--outline:before,
button.btn.btn--outline:before,
a.btn.btn--outline:before {
  content: '';
  position: absolute;
  background-color: var(--clr-primary);
  right: 100%;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: -1;
  transition: right 0.2s ease-in-out;
}

.btn.btn--outline:hover:before,
.btn.btn--outline:focus:before,
button.btn.btn--outline:hover:before,
button.btn.btn--outline:focus:before,
a.btn.btn--outline:hover:before,
a.btn.btn--outline:focus:before {
  right: 0;
}

/* XR: Se utilizó esta sintaxis para forzar el comportamiento y evitar que bootstrap alterara el diseño */
.btn.btn--plain, 
button.btn.btn--plain,
a.btn.btn--plain {
  color: var(--text-color) !important;
  text-transform: initial !important;
  background-color: var(--clr-bg-alt) !important;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px !important;
  border: 0 !important;
}

.btn--plain:hover {
  transform: translateY(-4px);
}

.btn--icon {
  padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
  color: var(--clr-primary);
}

.btn--icon:active {
  transform: translateY(-5px);
}



.btn--download {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;  /* XR: Originalmente relative, cómo cambiar al centro?  */
  overflow: hidden;
  z-index: 1;
}

.btn--back {
  display: inline-block;
  margin-top: 0rem; 
  margin-left: rem; 
  padding: 0.25rem 0.5rem;  
  background-color: transparent; 
  color: rgb(255, 255, 255);
  border: line;  
  border-radius: 0;  
  text-decoration: none;
  font-size: 0.875rem;  
  text-align: left;
}


@media (max-width: 600px) {
  .section {
    margin-top: 4em;
  }
}

/* XR: Creo que agregué esto para poder ajustar el comportamiento del fondo*/

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex-grow: 1;
}


/* XR: Agregué esto para centrar el contenido de la presentación en la página principal*/
.about {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

.about__desc {
  text-align: center;
  margin-top: 1rem;
  line-height: 1.7;
}


