.project-page {
  padding: 2rem 1rem;
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
}

.project-page__title {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  word-wrap: break-word;
}

.project-page h1 {
  margin-bottom: 1rem;
}

.project-page h3 {
  margin: 2rem 0 1rem;
}

.project-page ul {
  margin: 1rem 0;
  padding-left: 1.5rem;
}

.btn--back {
  display: inline-block;
  margin-top: -5rem; /* -3rem funciona bien, pero mueve todos los botones, redefinir solo para .btn--back (crear) */
  margin-left: 0rem; /* lo mismo pero con -1rem */
  padding: 0.25rem 0.5rem;  /* más pequeño */
  background-color: transparent;  /* fondo transparente */
  color: rgb(167, 170, 179);
  border: none;  /* sin bordes */
  border-radius: 0;  /* sin esquinas redondeadas, opcional */
  text-decoration: none;
  font-size: 0.875rem;  /* tamaño de fuente más pequeño */
  text-align: left;  /* orientado a la izquierda */
}


.project-page p {
  line-height: 1.5;
  margin-bottom: 1.5rem;
  font-size: 1rem;
  max-width: 100%;
}

.project-page__description {
  margin-bottom: 2rem;
}

/* Estilos para las imágenes */
.project-page__images {
  margin: 2rem 0;
}

.project-page__images-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.project-page__image-wrapper {
  margin: 0;
  width: 100%;
  box-shadow: var(--shadow);
  border-radius: 8px;
  overflow: hidden;
}

.project-page__image {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-page__image:hover {
  transform: scale(1.02);
}

.project-page__image-caption {
  padding: 0.75rem;
  font-size: 0.9rem;
  background-color: var(--clr-bg-alt);
  text-align: center;
}

/* Estilos para las herramientas */
.project-page__stack {
  margin: 2rem 0;
}

.project-page__stack ul {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.project-page__stack li {
  background-color: var(--clr-bg-alt);
  padding: 0.5rem 1rem;
  border-radius: 30px;
  font-size: 0.9rem;
  box-shadow: var(--shadow);
}

/* Enlaces del proyecto */
.project-page__links {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 2rem;
}

/* Ajustes para dispositivos móviles */
@media (max-width: 768px) {
  .project-page {
    padding: 1.5rem 1rem;
  }
  
  .project-page__title {
    font-size: 2rem;
  }
  
  .project-page__images-container {
    grid-template-columns: 1fr;
  }
  
  .project-page__stack ul {
    padding-left: 1rem;
  }
}

@media (max-width: 480px) {
  .project-page {
    padding: 1rem 0.75rem;
  }
  
  .project-page__title {
    font-size: 1.8rem;
  }
  
  .project-page h3 {
    font-size: 1.3rem;
  }
  
  .project-page p {
    font-size: 0.95rem;
  }
}