.project-pdf-container {
  margin: 1.5rem 0;
}

.project-pdf-title {
  text-align: center;
  margin-bottom: 1rem;
  color: var(--clr-fg-alt);
}

.project-pdf-controls {
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.project-page-info {
  margin: 0 1rem;
  color: var(--clr-fg);
}

.project-pdf-document-wrapper {
  display: flex;
  justify-content: center;
  position: relative;
  min-height: 500px;
}

.project-pdf-document {
  box-shadow: var(--shadow);
  background-color: var(--clr-bg-alt);
  border-radius: 5px;
  max-width: 100%;
}

.project-pdf-page {
  border-radius: 5px;
}

.project-pdf-loading, .project-pdf-error {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1rem;
  background-color: var(--clr-bg-alt);
  border-radius: 5px;
  box-shadow: var(--shadow);
  text-align: center;
}

.project-pdf-error {
  color: #ff6b6b;
}

/* XR: Estilos para dispositivos móviles */
@media (max-width: 500) {
  .project-pdf-controls {
    flex-direction: row-reverse;
    gap: 0.5rem;
  }
  
  .project-page-info {
    margin: 0.5rem 0;
  }
}