.pdf-viewer {
  margin: 2rem 0;
  width: 100%;
}

.pdf-viewer__title {
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.pdf-viewer__container {
  width: 100%;
  height: 600px;
  border: 1px solid var(--clr-fg);
  border-radius: 8px;
  overflow: hidden;
}

.pdf-viewer__iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.btn--control {
  display: inline-block;
  margin-top: 0rem; /* -3rem funciona bien, pero mueve todos los botones, redefinir solo para .btn--back (crear) */
  margin-left: 0rem; /* lo mismo pero con -1rem */
  padding: 0.25rem 0.5rem;  /* más pequeño */
  background-color: transparent;  /* fondo transparente */
  color: rgba(190, 192, 201, 0.815);
  border: none;  /* sin bordes */
  border-radius: 0;  /* sin esquinas redondeadas, opcional */
  text-decoration: none;
  font-size: 0.875rem;  /* tamaño de fuente más pequeño */
  text-align: left;  /* orientado a la izquierda */
}

/* Para PDFViewer responsivo */
@media (max-width: 768px) {
  .pdf-viewer__container {
    height: 500px;
  }
}

@media (max-width: 480px) {
  .pdf-viewer__container {
    height: 400px;
  }
  
  .pdf-viewer__title {
    font-size: 1.2rem;
  }
}

.pdf-controls {
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}